import React, { useState, useEffect } from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import Keyboard from "react-simple-keyboard";
import inputMask from "simple-keyboard-input-mask";
import "react-simple-keyboard/build/css/index.css";
import { useSelector, useDispatch } from "react-redux";
import { clockin } from "../../../actions/userActions";
import Webcam from "react-webcam";

const Clockin = ({ history }) => {
  const [identity, setIdentity] = useState("");
  const [status, setStatus] = useState("");
  const [latitude, setLatitude] = useState("");
  const [longitude, setLongitude] = useState("");
  const [media1, setMedia1] = useState("");

  const dispatch = useDispatch();

  const videoConstraints = {
    width: 400,
    height: 400,
    facingMode: "user",
  };

  const webcamRef = React.useRef(null);

  const capture = React.useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot();
    const slice = imageSrc.substring(23);
    setMedia1(slice);
  }, [webcamRef]);

  const getLocation = () => {
    if (!navigator.geolocation) {
      setStatus("Geolocation is not supported by your browser");
    } else {
      setStatus("Locating...");
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setStatus(null);
          setLatitude(position.coords.latitude);
          setLongitude(position.coords.longitude);
        },
        () => {
          setStatus("Unable to retrieve your location");
        }
      );
    }
  };
  useEffect(() => {
    getLocation();
  }, []);

  const login = (e) => {
    e.preventDefault();
    if (!latitude && !longitude) {
      setStatus("Geolocation is not supported by your browser");
    } else {
      dispatch(clockin(identity, latitude, media1, longitude));
    }
  };

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="We need some details" />
      <div className={styles.form}>
        {status && <div>{status}</div>}
        <div className={`${styles.formContainer} text-center`}>
          <Webcam
            audio={false}
            height={400}
            ref={webcamRef}
            screenshotFormat="image/jpeg"
            width={400}
            videoConstraints={videoConstraints}
          />
          <div className={styles.formContainer}>
            <button
              className={`btn btn-success form-control ${styles.expandBtn}`}
              onClick={capture}
            >
              Capture Photo
            </button>
          </div>
        </div>
        <form onSubmit={login}>
          <div className={styles.formContainer}>
            <input
              type="password"
              className="form-control"
              onChange={(e) => setIdentity(e.target.value)}
              placeholder="Pin"
              value={identity}
              readOnly
            />
            <Keyboard
              theme={"hg-theme-default"}
              layoutName="default"
              onChange={(input) => setIdentity(input)}
              onKeyPress={(e) => setIdentity(e)}
              disableCaretPositioning={true}
              layout={{
                default: ["1 2 3", "4 5 6", "7 8 9", "{//} 0 {//}", "{bksp}"],
                shift: ["! / #", "$ % ^", "& * (", "{shift} ) +", "{bksp}"],
              }}
              inputMask={"(99) 9999-9999"}
              modules={[inputMask]}
            />
          </div>
          <div className={styles.formContainer}>
            <div className="col-md-3 float-start">
              <Link
                to="/"
                className={`btn btn-dark form-control  ${styles.expandBtn}`}
              >
                <FiArrowLeftCircle /> Back
              </Link>
            </div>
            <div className="col-md-3 float-end">
              <button
                type="submit"
                className={`btn btn-success form-control ${styles.expandBtn}`}
                value="Next"
              >
                <FiArrowRightCircle /> Login
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Clockin;
