import React from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import Webcam from "react-webcam";
import { useDispatch, useSelector } from "react-redux";
import { registration } from "../../../actions/userActions";

const VerifyScreen = ({ history }) => {
  const dispatch = useDispatch();

  const userRegistration = useSelector((state) => state.userRegistration);
  const { loading, error, success } = userRegistration;

  const videoConstraints = {
    width: 400,
    height: 400,
    facingMode: "user",
  };

  const webcamRef = React.useRef(null);

  const capture = React.useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot();
    const firstName = localStorage.getItem("firstName");
    const lastName = localStorage.getItem("lastName");
    const email = localStorage.getItem("email");
    const identity = localStorage.getItem("identity");
    const media1 = localStorage.getItem("media1");
    localStorage.setItem("media2", imageSrc);
    const media = localStorage.getItem("media2");
    const media2 = media.substring(23);
    dispatch(
      registration(firstName, lastName, identity, email, media1, media2)
    );
  }, [webcamRef, dispatch]);
  if (success) {
    history.push("/success");
  }
  return (
    <div className={styles.app}>
      <Card title="Welcome" content="Take another Picture" />
      <div className={styles.form}>
        {error && <div className="alert alert-danger">{error}</div>}
        {success && <div className="alert alert-success">{success}</div>}
        {loading ? (
          <div></div>
        ) : (
          <>
            <form onSubmit={capture}>
              <div className={`${styles.formContainer} text-center`}>
                <Webcam
                  audio={false}
                  height={400}
                  ref={webcamRef}
                  screenshotFormat="image/jpeg"
                  width={400}
                  videoConstraints={videoConstraints}
                />
              </div>
              <div className={styles.formContainer}>
                <div className="col-md-3 float-start">
                  <Link
                    to="/"
                    className={`btn btn-dark form-control  ${styles.expandBtn}`}
                  >
                    <FiArrowLeftCircle /> Back
                  </Link>
                </div>
                <div className="col-md-3 float-end">
                  <button
                    type="submit"
                    className={`btn btn-success form-control ${styles.expandBtn}`}
                    value="Next"
                  >
                    <FiArrowRightCircle /> Next
                  </button>
                </div>
              </div>
            </form>
          </>
        )}
      </div>
    </div>
  );
};

export default VerifyScreen;
