import {
  USER_REGISTER_FAIL,
  USER_REGISTER_SUCCESS,
  USER_REGISTER_REQUEST,
  USER_REGISTER_RESET,
  USER_CLOCKIN_FAIL,
  USER_CLOCKIN_REQUEST,
  USER_CLOCKIN_RESET,
  USER_CLOCKIN_SUCCESS,
} from "../constants/userConstants";

export const userRegisterReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_REGISTER_REQUEST:
      return { loading: true };
    case USER_REGISTER_SUCCESS:
      return { loading: false, register: action.payload, success: true };
    case USER_REGISTER_FAIL:
      return { loading: false, error: action.payload };
    case USER_REGISTER_RESET:
      return {};
    default:
      return state;
  }
};

export const userClockinReducer = (state = { clockin: {} }, action) => {
  switch (action.type) {
    case USER_CLOCKIN_REQUEST:
      return { loading: true };
    case USER_CLOCKIN_SUCCESS:
      return { loading: false, clockin: action.payload, success: true };
    case USER_CLOCKIN_FAIL:
      return { loading: false, error: action.payload };
    case USER_CLOCKIN_RESET:
      return {};
    default:
      return state;
  }
};
