import React, { useState } from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import Keyboard from "react-simple-keyboard";
import inputMask from "simple-keyboard-input-mask";
import "react-simple-keyboard/build/css/index.css";

const ClockOut = ({ history }) => {
  const [identity, setIdentity] = useState("");

  const submitHandler = (e) => {
    e.preventDefault();

    localStorage.setItem("identity", identity);
    history.push("/attendance/capture");
  };

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="We need some details" />
      <div className={styles.form}>
        <form onSubmit={submitHandler}>
          <div className={styles.formContainer}>
            <input
              type="password"
              className="form-control"
              onChange={(e) => setIdentity(e.target.value)}
              placeholder="Pin"
              value={identity}
              readOnly
            />
            <Keyboard
              theme={"hg-theme-default"}
              layoutName="default"
              onChange={(input) => setIdentity(input)}
              onKeyPress={(e) => setIdentity(e)}
              disableCaretPositioning={true}
              layout={{
                default: ["1 2 3", "4 5 6", "7 8 9", "{//} 0 {//}", "{bksp}"],
                shift: ["! / #", "$ % ^", "& * (", "{shift} ) +", "{bksp}"],
              }}
              inputMask={"(99) 9999-9999"}
              modules={[inputMask]}
            />
          </div>
          <div className={styles.formContainer}>
            <div className="col-md-3 float-start">
              <Link
                to="/"
                className={`btn btn-dark form-control  ${styles.expandBtn}`}
              >
                <FiArrowLeftCircle /> Back
              </Link>
            </div>
            <div className="col-md-3 float-end">
              <button
                type="submit"
                className={`btn btn-success form-control ${styles.expandBtn}`}
                value="Next"
              >
                <FiArrowRightCircle /> Next
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default ClockOut;
