import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Attendance from "./components/screens/ams/Attendance";
import CaptureScreen from "./components/screens/ams/Capture";
import Clockin from "./components/screens/ams/Clockin";
import ClockOut from "./components/screens/ams/ClockOut";
import Enrollment from "./components/screens/ams/Enrollment";
import Home from "./components/screens/Home";
import HostScreen from "./components/screens/new/Host";
import LaptopScreen from "./components/screens/new/LaptopScreen";
import NewGuest from "./components/screens/new/NewGuest";
import SnapshotScreen from "./components/screens/new/Snapshot";
import Success from "./components/screens/Success";
import VerifyScreen from "./components/screens/ams/VerifyCapture";
import VerifyInfo from "./components/screens/new/VerifyInfo";
import ReturningGuest from "./components/screens/returning/ReturningGuest";
import GuestInfo from "./components/screens/returning/GuestInfo";
import RLaptop from "./components/screens/returning/RLaptop";
import RHost from "./components/screens/returning/RHost";
import RSnapshot from "./components/screens/returning/RSnapshot";
import RVerifyInfo from "./components/screens/returning/RVerifyInfo";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/newGuest" exact component={NewGuest} />
        <Route path="/laptop" exact component={LaptopScreen} />
        <Route path="/host" exact component={HostScreen} />
        <Route path="/snapshot" exact component={SnapshotScreen} />
        <Route path="/success" exact component={Success} />
        <Route path="/verifyInfo" exact component={VerifyInfo} />
        <Route path="/attendance" exact component={Attendance} />
        <Route path="/attendance/enrollment" exact component={Enrollment} />
        <Route path="/attendance/capture" exact component={CaptureScreen} />
        <Route path="/attendance/verification" exact component={VerifyScreen} />
        <Route path="/attendance/clockin" exact component={Clockin} />
        <Route path="/attendance/clockout" exact component={ClockOut} />
        <Route path="/returning" exact component={ReturningGuest} />
        <Route path="/returning/userInfo" exact component={GuestInfo} />
        <Route path="/returning/laptop" exact component={RLaptop} />
        <Route path="/returning/host" exact component={RHost} />
        <Route path="/returning/snapshot" exact component={RSnapshot} />
        <Route path="/returning/verifyInfo" exact component={RVerifyInfo} />
      </Switch>
    </Router>
  );
}

export default App;
